body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.motherContainer{display:flex;flex-direction:column;height:100vh;width:100vw}.motherContainer .search{margin-top:2rem;display:flex;justify-content:flex-end;padding-right:3em;width:100vw;height:10vw}.motherContainer .search form{align-self:center}.motherContainer .result{position:relative;flex-grow:1;display:flex;height:80vw;flex-direction:row}.motherContainer .result .map{position:relative;height:100%;width:50%}.motherContainer .result .table{padding:3rem;width:50%}

